                                    Cookies
                                    <ol>
                                        <li>
                                            Cookies are small text files, they cannot run on your computer, search your computer for other information, or transmit any information to anyone. A cookie containing a unique identification number that is transferred (through your browser) from a website to the hard drive of your computer. The cookie identifies your browser but will not let a website know any Personal Information about you, such as your name or address. These files are then used by such Sites as a kind of electronic identification tag when users revisit that Site. As with many major websites, Our Site use cookies. Cookies are used by the Site so that we can recognize you when you return to the Site and personalize your settings and preferences. Most browsers are initially set up to accept cookies.
                                        </li>
